<template>
    <transition name="playing">
    <div class="playing">
        <div class="page-cover" style="position:fixed;top:0;left:0;background: #060506;width:100vw;height:100vh;"></div>
        <div class="bg-img">
            <img :src="'https://y.gtimg.cn/music/photo_new/T002R90x90M000'+ curSong.albummid +'.jpg?max_age=2592000'" alt="">
        </div>

        <div class="playing-header">
            <h1><i class="down-close" @click="close"></i>{{curSong.singer[0].name}}</h1>
            <p>{{curSong.songname}}</p>
        </div>

        <!-- S 封面 -->
        <div class="cover" :class="{'playing-run': playState == 'playing'}" ref="cover">
            <img :src="'https://y.gtimg.cn/music/photo_new/T002R90x90M000'+ curSong.albummid +'.jpg?max_age=2592000'" alt="">
        </div>
        <!-- E 封面 -->

        <!-- S 操作区域 -->
        <div class="opt-area">
            <a class="opt-mode" href="javascript:;"></a>
            <a class="opt-prev" href="javascript:;" @click="prev"></a>
            <a :class="[playState == 'playing' ? 'opt-play-pused' : 'opt-play-playing']" href="javascript:;" @click="playState == 'playing' ? paused() : play()"></a>
            <a class="opt-next" href="javascript:;" @click="next"></a>
            <a class="opt-like" href="javascript:;"></a>
        </div>
        <!-- E 操作区域 -->  
    </div>
    </transition>
</template>

<script>
import {mapGetters, mapMutations} from 'vuex'
export default {
    data: function() {
        return {
            playState: 'paused',
            coverNode: null,
            sibMiniComPlayer: null
        }
    },
    computed: {
        ...mapGetters({
            // 当前歌曲
            curSong: 'getCurSong',
            songList: 'getPlayList',
            curIndex: 'getCurIndex'

        })
    },
    created() {
        this.playState = 'playing'
    },
    mounted() {
        this.coverNode = this.$refs.cover

        // 获取MiniPlayer组件中的audio节点
        this.sibMiniComPlayer = this.$parent.$children[2].$refs.player
    },
    methods: {
        ...mapMutations([
            'SET_CUR_INDEX'
        ]),
        // 暂停
        paused() {
            this.coverNode.style.animationPlayState = 'paused'
            this.playState = 'paused'

            // 设置audio暂停播放
            this.sibMiniComPlayer.pause() 
        },
        // 播放
        play() {
            this.coverNode.style.animationPlayState = 'running'
            this.playState = 'playing'

            // 设置audio播放
            this.sibMiniComPlayer.play()
        },
        // 上一首
        prev() {
            let len = this.songList.length
            let index = this.curIndex - 1 < 0 ? len - 1 : this.curIndex - 1
            this.SET_CUR_INDEX(index)
        },
        // 下一首
        next() {
            let len = this.songList.length
            let index = this.curIndex + 1 > len - 1 ? 0 : this.curIndex + 1
            this.SET_CUR_INDEX(index)
        },
        // 关闭播放页面，跳转至前一个页面
        close() {
            this.$router.go(-1)
        }
    },
    components: {
    }
}
</script>
<style scoped lang="scss">
@import 'SCSS/animate.scss';
@import 'SCSS/playing';

.playing-enter-active{
    animation: slideInRight .5s;
}
.playing-leave-active{
    animation: slideOutRight .5s;
}
</style>